// Layout 

.l-main{
    overflow: hidden;
}
.bd-container{
    max-width: 968px;
    width: calc(100% - 3rem);
    margin: 0 1.5rem;
  
}

.bd-grid{
    display: grid;
    gap: 1.5rem;
}

.l-header{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: z-fixed;
    background-color: var(--body-color);
}


